const round1_text = "This is similar to the classic game ConnectFour. Play with a partner with a particular focus on what strategy you are using to win! Play no more than four games!"
const round2_text = "In this round, you can get tokens that aren’t fully red or white. The portion that is your color expresses the probability it will be measured as your color (in the picture, that token has a 75% chance of being yellow). When it hits the bottom, it gets measured, and you see the outcome. Play at least 2 games with your partner. Focus on how your strategy changes based on what probability you have of getting your color!"
const round3_text = "When you drop your pieces, they won’t be measured right away. Instead, it waits until all pieces have been played before measuring them. The computer measures them in the same order in which they were dropped, and you will find out who won (if anyone). Focus again on strategy!"
const round4_text = "When you end the game, instead of the computer measuring one at a time in the same order than they were dropped, you and your opponent trade off choosing which token to measure. How does that change your strategy?"

