import React from "react";
import { motion } from "framer-motion";
import YoutubeEmbed from '../components/YoutubeEmbed'
import Layout from "../components/layout";
import img0 from "../assets/QueueBits/0.png"
import img1 from "../assets/QueueBits/1.png"
import img2 from "../assets/QueueBits/2.png"
import img3 from "../assets/QueueBits/3.png"

const content = {
  animate: {
    transition: { staggerChildren: 0.1 },
  },
};

const title = {
  initial: { y: -20, opacity: 0 },
  animate: {
    y: 0,
    opacity: 1,
    transition: {
      duration: 0.7,
      ease: [0.6, -0.05, 0.01, 0.99],
    },
  },
};

const makeSection = (title, paragraph, img, left) => {
        let floatDir = left ? 'left' : 'right'
        return (
            <div style={{minHeight:400}}>
                <h1 className="mb-4 text-2xl font-medium text-gray-900 sm:text-3xl title-font">{title}</h1>
                <img src={img} width={300} style={{float:floatDir, margin:'15px'}}/>
                <p className="Main">
                        {paragraph}
                </p>
            </div>)
    }

    const makeGameSection = (title, paragraph, img, left, link) => {
        let floatDir = left ? 'left' : 'right'
        return (
            <div style={{minHeight:400}}>
                <h1 className="mb-4 text-2xl font-medium text-gray-900 sm:text-3xl title-font">{title}</h1>
                <img src={img} width={300} style={{float:floatDir, margin:'15px'}}/>
                <p className="Main" style={{minHeight:150}}>
                        {paragraph}
                </p>
                <a href={link}>
                  <button className="flex px-8 py-2 mx-auto text-lg text-white bg-red-500 border-0 rounded focus:outline-none hover:bg-red-600">
                    Play
                  </button>
                </a>
            </div>)
    }

const round1_text = "This is similar to the classic game ConnectFour. Play with a partner with a particular focus on what strategy you are using to win! Play no more than four games!"
const round2_text = "In this round, you can get tokens that aren’t fully red or white. The portion that is your color expresses the probability it will be measured as your color (in the picture, that token has a 75% chance of being yellow). When it hits the bottom, it gets measured, and you see the outcome. Play at least 2 games with your partner. Focus on how your strategy changes based on what probability you have of getting your color!"
const round3_text = "When you drop your pieces, they won’t be measured right away. Instead, it waits until all pieces have been played before measuring them. The computer measures them in the same order in which they were dropped, and you will find out who won (if anyone). Focus again on strategy!"
const round4_text = "When you end the game, instead of the computer measuring one at a time in the same order than they were dropped, you and your opponent trade off choosing which token to measure. How does that change your strategy?"

const SuperPosition = () => {
  return (
    <Layout>
      <motion.section
        exit={{ opacity: 0 }}
        className="relative text-gray-700 body-font"
      >
        <motion.div
          variants={content}
          animate="animate"
          initial="initial"
          className="container px-5 py-24 mx-auto"
        >
          <motion.div
            variants={title}
            className="flex flex-col w-full mb-12 text-center"
            style={{tableLayout: "fixed"}}
          >
            <h1 className="mb-4 text-2xl font-medium text-gray-900 sm:text-3xl title-font">
              Probability, Superposition, and Measurement
            </h1>
            <p className="mx-auto text-base leading-relaxed lg:w-2/3">
              Welcome to the Probability, Superposition, and Measurement Quantime activity! To start out, watch this video about Quantum Computing:
            </p>
            <br/>
            <YoutubeEmbed embedId="OWJCfOvochA" />
            <br/>
            <p>
            This activity explores the relationship between superposition, probability, and measurement, which are critical to the unique properties of quantum technologies.
            You are going to do four rounds of Queue Bits play. You will play with a partner next to you in class. Make sure to observe all COVID restrictions that may be in place as you play! You may need one person to control the mouse if necessary! 
            </p>
            <br/>
            <br/>
            <a href="https://chord.cs.uchicago.edu/Quander/ConnectFour/">
                  <button className="flex px-8 py-2 mx-auto text-lg text-white bg-red-500 border-0 rounded focus:outline-none hover:bg-red-600">
                    Play Game!
                  </button>
            </a>
            <br/>
            <br/>
            {makeSection("Round 1 (5 minutes):Classical Play", round1_text, img0, false)}
            <br/>
            <br/>
            <br/>
            {makeSection("Round 2 (8 minutes): Superposition, measurement upon drop", round2_text, img1, true)}
            <br/>
            <br/>
            <br/>
            {makeSection("Round 3 (8 minutes): Measurement occurs at the end, in the same order in which they were dropped.", round3_text, img2, false)}
            <br/>
            <br/>
            <br/>
            {makeSection("Round 4 (10 minutes): Measurement occurs at the end; players trade off choosing which token to measure.", round4_text, img3, true)}
            <YoutubeEmbed embedId="Gs9lxdL3BC8" />
          </motion.div>
        </motion.div>
      </motion.section>
    </Layout>
  );
};

export default SuperPosition;
