import React from "react";
import { Switch, Route, useLocation, useHistory } from "react-router-dom";
import { AnimatePresence } from "framer-motion";

import HomePage from "./pages/home"
import SuperPosition from "./pages/superposition"
import QuantumOperations from "./pages/operations"

const App = () => {
  const [isFirstMount, setIsFirstMount] = React.useState(true);
  const location = useLocation();
  const history = useHistory();

  React.useEffect(() => {
    const unlisten = history.listen(() => {
      isFirstMount && setIsFirstMount(false);
    });

    return unlisten;
  }, [history, isFirstMount]);

  return (
    <AnimatePresence exitBeforeEnter>
      <Switch location={location} key={location.pathname}>
        <Route
          path="/"
          exact
          component={HomePage}
        />
        <Route path="/superposition" component={SuperPosition} />
        <Route path="/quantum_operations" component={QuantumOperations} />
      </Switch>
    </AnimatePresence>
  );
};

export default App;
